body {
  font-family: "Noto Serif JP", "Noto Sans JP", sans-serif;
  font-size: 1.25vw;
  line-height: 1.4366667;
  color: #0B1D26;
}
@media screen and (min-width: 1920px) {
  body {
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 767px) {
  body {
    font-size: 3.05vw;
  }
}

img {
  max-width: 100%;
  height: auto;
}

a, button {
  transition: 0.3s;
}
a:hover, button:hover {
  opacity: 0.75;
}

